@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

canvas{background-color:white}.nav{padding:0 10vmin;display:flex;font-family:'Poiret One', cursive;justify-content:space-between;align-items:center}.nav__btn{font-family:'Poiret One', cursive;font-weight:bold;color:white;width:30px;height:30px;border-radius:50%;border:none;padding:0;position:relative}.nav__btn_wrapper{display:flex;align-items:center;transition:-webkit-filter 1s;transition:filter 1s;transition:filter 1s, -webkit-filter 1s}.nav__btn:focus{outline:none}.nav__btn:hover{cursor:pointer}.nav__btn_img{width:30px;height:30px;z-index:0;box-sizing:border-box;border-radius:50%}.nav__btn_img-active{border:2px solid white}

.NavBar{width:90%;display:flex;justify-content:space-between;align-items:center;text-align:center}.NavBar__link{padding:5px 10px;font-weight:bolder;letter-spacing:1px;text-decoration:none;position:relative;z-index:0;display:inline-block;overflow:hidden;color:#333;transition:color .3s ease-out;border-radius:3px}.NavBar__link_active{border-radius:3px;background-image:linear-gradient(60deg, black 0%, grey 100%);color:white}.NavBar__link::before{content:"";position:absolute;z-index:-1;top:0;left:0;transform:translateY(calc(100% - 2px));width:100%;height:100%;background-image:linear-gradient(60deg, black 0%, grey 100%);transition:transform .25s ease-out}.NavBar__link:hover{color:#fff}.NavBar__link:hover::before{transform:translateY(0);transition:transform .25s ease-out}

